body {
  text-align: center;
  color: #fff;
  text-shadow: 0 0 4px #000, 0 0 4px #000;
  margin: 0;
  font-family: Arial;
  font-size: 15px;
  overflow: hidden;
}

#mySlider {
  appearance: none;
  width: 90%;
  height: 15px;
  z-index: 1;
  background: #47f;
  border-radius: 5px;
  margin-top: 35px;
}

#mySlider::-webkit-slider-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  background: #000;
  border-radius: 5px;
}

video {
  width: 100%;
  height: 100%;
  z-index: -1;
  align-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#heightInfo {
  width: 100%;
  border-bottom: 3px solid #000;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%);
}

